import React, { Component } from 'react';
import { DatePicker, Select, InputNumber, Icon, message } from 'antd';
import moment from 'moment';
import * as ApiProvider from '../../API/ApiProvider'

import VitalWarningValueRow from './VitalWarningValueRow'
import './Vitalsign.scss'

export default class VitalWarningValue extends Component {
  selectTime = () => {
    const nowTime = moment().format('HH');
    const selectItem = []
    const { Option } = Select;
    for (let index = 0; index < 24; index++) {
      if (index > Number(nowTime)) {
        selectItem.push(<Option value={index}>{index}</Option >)
      }
    }
    return (
      <Select id="selectFrom" placeholder="請選擇時間" style={{ width: "120px" }}>
        {selectItem}
      </Select>
    )
  }

  addData = () => {
    const { fixStatus } = this.props
    let database = undefined
    let date = undefined

    if (fixStatus === true) {
      database = this.props.element
      date = moment(database.centerwarningvaluekey.time, "YYYYMMDDHHmmss")
    }

    const selectDate = fixStatus ? date.format("YYYY/MM/DD") : document.querySelector('#modelDate').querySelector('.ant-calendar-picker-input').value
    const selectTime = fixStatus ? date.format("HH") : document.querySelector('#selectFrom').querySelector('.ant-select-selection-selected-value').innerHTML
    console.log(selectDate, selectTime)

    if (selectDate == '' || selectTime == '') {
      message.error('The Date is not selected', 1);
    }
    else {
      const saveData = {
        'centerwarningvaluekey': {
          'id': this.props.id,
          'time': moment(selectDate + selectTime, "YYYY/MM/DDHH").format("YYYYMMDDHHmmss"),
        },
        'hr_hight': document.querySelector('#hrhi').value == '' ? '160' : document.querySelector('#hrhi').value,
        'hr_low': document.querySelector('#hrlo').value == '' ? '100' : document.querySelector('#hrlo').value,
        'sbp_hight': document.querySelector('#sbphi').value == '' ? '55' : document.querySelector('#sbphi').value,
        'sbp_low': document.querySelector('#sbplo').value == '' ? '35' : document.querySelector('#sbplo').value,
        'dbp_hight': document.querySelector('#dbphi').value == '' ? '75' : document.querySelector('#dbphi').value,
        'dbp_low': document.querySelector('#dbplo').value == '' ? '55' : document.querySelector('#dbplo').value,
        'rr_hight': document.querySelector('#rrhi').value == '' ? '64' : document.querySelector('#rrhi').value,
        'rr_low': document.querySelector('#rrlo').value == '' ? '20' : document.querySelector('#rrlo').value,
        'spo2_hight': document.querySelector('#sphi').value == '' ? '100' : document.querySelector('#sphi').value,
        'spo2_low': document.querySelector('#splo').value == '' ? '90' : document.querySelector('#splo').value,
        'bt_hight': document.querySelector('#bthi').value == '' ? '38' : document.querySelector('#bthi').value,
        'bt_low': document.querySelector('#btlo').value == '' ? '36' : document.querySelector('#btlo').value,
      }
      ApiProvider.createCenterWarningValue(saveData)
        .then(data => {
          this.props.review()
          if (fixStatus) {
            this.props.cancelFix()
          } else {
            this.props.cancelAdd()
          }
        })
        .catch(err => { console.log(err) })
    }
  }

  cancelAdd = () => {
    const { fixStatus } = this.props
    if (fixStatus) {

    } else {
      this.props.cancelAdd()
    }
  }

  render() {
    const { fixStatus } = this.props
    let database = undefined
    let date = undefined

    if (fixStatus === true) {
      database = this.props.element
      date = moment(database.centerwarningvaluekey.time, "YYYYMMDDHHmmss").format("YYYY/MM/DD HH:00")
    }

    return (
      <div style={{ display: "grid", gridTemplateColumns: "2fr 1fr 1fr 1fr 1fr 1fr 1fr 0.5fr", border: "1px solid rgba(232, 232, 232, 1)", borderTop: "0px", alignContent: "center", height: "60px" }}>
        {fixStatus ? <div id='DataDate' style={{ display: "grid", alignContent: "center", fontSize: "14px", paddingLeft: "10px" }}>{date}</div>
          :
          <div style={{ display: "grid", gridTemplateColumns: "130px auto", alignContent: "center", fontSize: "14px", paddingLeft: "10px" }}>
            <div className="setDate" >
              <DatePicker id='modelDate' disabledDate={(current) => { return current < moment().startOf("day") }} />
            </div>
            <div className="setHour">
              {this.selectTime()}
            </div>
          </div>}
        <div style={{ display: "grid", gridTemplateRows: "1fr 1fr", fontSize: "14px", paddingLeft: "10px", alignContent: "center" }}>
          <div style={{ display: "grid", gridTemplateColumns: "50px auto" }}>
            <div style={{ width: "50px" }}>Hight :</div>
            <div className="inputset">
              <InputNumber id="hrhi" size="small" defaultValue={fixStatus ? database.hr_hight : null} min={0} max={888} />
            </div>
          </div>
          <div style={{ display: "grid", gridTemplateColumns: "50px auto", marginTop: "1px" }}>
            <div>Low :</div>
            <div className="inputset">
              <InputNumber id="hrlo" size="small" defaultValue={fixStatus ? database.hr_low : null} min={0} max={888} />
            </div>
          </div>
        </div>
        <div style={{ display: "grid", gridTemplateRows: "1fr 1fr", fontSize: "14px", paddingLeft: "10px", alignContent: "center" }}>
          <div style={{ display: "grid", gridTemplateColumns: "50px auto" }}>
            <div style={{ width: "50px" }}>Hight :</div>
            <div className="inputset">
              <InputNumber id="sbphi" size="small" defaultValue={fixStatus ? database.sbp_hight : null} min={0} max={888} />
            </div>
          </div>
          <div style={{ display: "grid", gridTemplateColumns: "50px auto", marginTop: "1px" }}>
            <div>Low :</div>
            <div className="inputset">
              <InputNumber id="sbplo" size="small" defaultValue={fixStatus ? database.sbp_low : null} min={0} max={888} />
            </div>
          </div>
        </div>
        <div style={{ display: "grid", gridTemplateRows: "1fr 1fr", fontSize: "14px", paddingLeft: "10px", alignContent: "center" }}>
          <div style={{ display: "grid", gridTemplateColumns: "50px auto" }}>
            <div style={{ width: "50px" }}>Hight :</div>
            <div className="inputset">
              <InputNumber id="dbphi" size="small" defaultValue={fixStatus ? database.dbp_hight : null} min={0} max={888} />
            </div>
          </div>
          <div style={{ display: "grid", gridTemplateColumns: "50px auto", marginTop: "1px" }}>
            <div>Low :</div>
            <div className="inputset">
              <InputNumber id="dbplo" size="small" defaultValue={fixStatus ? database.dbp_low : null} min={0} max={888} />
            </div>
          </div>
        </div>
        <div style={{ display: "grid", gridTemplateRows: "1fr 1fr", fontSize: "14px", paddingLeft: "10px", alignContent: "center" }}>
          <div style={{ display: "grid", gridTemplateColumns: "50px auto" }}>
            <div style={{ width: "50px" }}>Hight :</div>
            <div className="inputset">
              <InputNumber id="rrhi" size="small" defaultValue={fixStatus ? database.rr_hight : null} min={0} max={888} />
            </div>
          </div>
          <div style={{ display: "grid", gridTemplateColumns: "50px auto", marginTop: "1px" }}>
            <div>Low :</div>
            <div className="inputset">
              <InputNumber id="rrlo" size="small" defaultValue={fixStatus ? database.rr_low : null} min={0} max={888} />
            </div>
          </div>
        </div>
        <div style={{ display: "grid", gridTemplateRows: "1fr 1fr", fontSize: "14px", paddingLeft: "10px", alignContent: "center" }}>
          <div style={{ display: "grid", gridTemplateColumns: "50px auto" }}>
            <div style={{ width: "50px" }}>Hight :</div>
            <div className="inputset">
              <InputNumber id="sphi" size="small" defaultValue={fixStatus ? database.spo2_hight : null} min={0} max={888} />
            </div>
          </div>
          <div style={{ display: "grid", gridTemplateColumns: "50px auto", marginTop: "1px" }}>
            <div>Low :</div>
            <div className="inputset">
              <InputNumber id="splo" size="small" defaultValue={fixStatus ? database.spo2_low : null} min={0} max={888} />
            </div>
          </div>
        </div>
        <div style={{ display: "grid", gridTemplateRows: "1fr 1fr", fontSize: "14px", paddingLeft: "10px", alignContent: "center" }}>
          <div style={{ display: "grid", gridTemplateColumns: "50px auto" }}>
            <div style={{ width: "50px" }}>Hight :</div>
            <div className="inputset">
              <InputNumber id="bthi" size="small" defaultValue={fixStatus ? database.bt_hight : null} min={0} max={888} />
            </div>
          </div>
          <div style={{ display: "grid", gridTemplateColumns: "50px auto", marginTop: "1px" }}>
            <div>Low :</div>
            <div className="inputset">
              <InputNumber id="btlo" size="small" defaultValue={fixStatus ? database.bt_low : null} min={0} max={888} />
            </div>
          </div>
        </div>
        <div className="checkButton">
          <Icon type="save" style={{ fontSize: "26px" }} onClick={this.addData} />
          <Icon type="close" style={{ fontSize: "26px", marginLeft: "5px" }} onClick={this.cancelAdd} />
        </div>
      </div >
    );
  }
}
